/**
  * Syntax highlighting styles
  */

.highlighttable,
.highlight {
  font-family: "Fira Mono", Monaco, Consolas, monospace;
  font-weight: 400;
  background-color:#3f3f3f;
  border-radius: 4px;
  font-size: 0.675em;
  -webkit-font-smoothing: subpixel-antialiased;
  margin: 32px 0;
}

.highlight {

  .hll{background-color:#222}
  .c{color:#7f9f7f}
  .err{color:#e37170;background-color:#3d3535}
  .g{color:#7f9f7f}
  .k{color:#f0dfaf}
