  .cm{color:#7f9f7f}
  .cp{color:#7f9f7f}
  .c1{color:#7f9f7f}
  .cs{color:#cd0000;font-weight:bold}
  .gd{color:#cd0000}
  .ge{color:#ccc;font-style:italic}
  .gr{color:red}
  .gh{color:#dcdccc;font-weight:bold}
  .gi{color:#00cd00}
  .go{color:gray}
  .gp{color:#dcdccc;font-weight:bold}
  .gs{color:#ccc;font-weight:bold}
  .gu{color:purple;font-weight:bold}
  .gt{color:#0040D0}
  .kc{color:#dca3a3}
  .kd{color:#ffff86}
  .kn{color:#dfaf8f;font-weight:bold}
  .kp{color:#cdcf99}
  .kr{color:#cdcd00}
  .kt{color:#00cd00}
  .ld{color:#cc9393}
  .m{color:#8cd0d3}
  .s{color:#cc9393}
  .na{color:#9ac39f}
  .nb{color:#efef8f}
  .nc{color:#efef8f}
  .no{color:#ccc}
  .nd{color:#ccc}
  .ni{color:#c28182}
  .ne{color:#c3bf9f;font-weight:bold}
  .nf{color:#efef8f}
  .nl{color:#ccc}
  .nn{color:#8fbede}
  .nx{color:#ccc}
  .py{color:#ccc}
  .nt{color:#9ac39f}
  .nv{color:#dcdccc}
  .ow{color:#f0efd0}
  .w{color:#ccc}
  .mf{color:#8cd0d3}
  .mh{color:#8cd0d3}
  .mi{color:#8cd0d3}
  .mo{color:#8cd0d3}
