  .bp{color:#efef8f}
  .vc{color:#efef8f}
  .vg{color:#dcdccc}
  .vi{color:#ffffc7}
  .il{color:#8cd0d3}

}

.highlight {
  line-height: 1;
  overflow-x: scroll;
  padding: 10px;

  code,
  pre {
    color:#fdce93;
    background-color:#3f3f3f;
    border: 0;
  }

  pre {
    margin: 0;
    white-space: pre;
  }

  .lineno{
    border-right: solid 1px #dcdccc;
    padding-left: 10px;
    padding-right: 10px;
    text-align: right;
  }

  span {
    display: inline-block;
    padding: 0.3em 0;
  }
}

.highlighttable {

  .linenos {
    border-right: solid 1px #dcdccc;
    padding-left:10px;
    padding-right: 10px;
    text-align: right;
    width: 3em;
  }

  .code .highlight {
    padding-left: 10px;

    pre { white-space: pre; }
  }
}
